body{
    // background-image: url("../images/body-texture.png");
    // background-repeat: repeat;
    background-color: $white; 
}

.section{
    padding: 130px 0;
}

.form-control{
    height: 55px;
    padding: 15px;
    font-size: 14px;
    border-radius: 3px;
    margin-bottom: 20px;
    background: transparent;
    &:focus{
        outline: 0;
        box-shadow: none;
        border-color: $primary-color;
        background: transparent;
    }
}

select.form-control:not([size]):not([multiple]){
    height: 55px;
}

textarea.form-control{
    height: auto;
}

.custom-control-label{
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 600;
    cursor: pointer;
    font-family: $primary-font;
    margin-bottom: 15px;
    // &::before, &::after{
    //     top: 0;
    // }
}

.input-group{
    .form-control{
        border-right: 0;
    }
    &-text{
        background: transparent;
        border-left: 0;
    }
}

.category-content{
    .title{
        ul{
            .list-inline-item{
                &:not(:last-child){
                    margin-right: 3px;
                }
                font-family: $primary-font;
                i{
                    font-size: 10px;
                    margin-right: 7px;
                }
                &:last-child{
                    font-weight: 600;
                }
            }
        }
    }
    box-shadow: 0 3px 5px 3px rgba($color: $black, $alpha: .08); 
    padding: 20px 20px 0;
    border-radius: 4px;
}

.category-item-list{
    .category-item{
        padding: 20px;
        &:not(:last-child){
            border-bottom: 2px dashed $border-color;
        }
        h5{
            span{
                color: $text-color;
                font-size: 16px;
            }
        }
        ul.description{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            list-style: none;
            padding-left: 0;
            margin-bottom: 10px;
            margin-top: 10px;
            li{
                font-weight: 600;
                span{
                    font-weight: 400;
                    margin-left: 10px;
                    display: inline-block;
                }
                &:not(:last-child){
                    margin-right: 20px;
                }
                &:last-child{
                    display: flex;
                    align-items: center;
                    input{
                        text-align: center;
                        height: 45px;
                        width: 120px;
                    }
                    .bootstrap-touchspin{
                        margin-left: 10px;
                        span{
                            margin-left: 0;
                        }
                        button{
                            border-radius: 0;
                            height: 100%;
                        }
                    }
                }
            }
        }
        .total{
            display: flex;
            align-items: center;
            h6{
                margin-bottom: 0;
                margin-right: 30px;
            }
        }
    }
}

.order-summery{
    padding: 30px 20px;
    background: $white;
    margin-top: 50px;
    border-radius: 6px;
    box-shadow: 0 3px 5px 3px rgba($color: $black, $alpha: .08);
    .summery{
        display: flex;
        margin-bottom: 30px;
        justify-content: center;
        div{
            flex-basis: 25%;
        }
    }
}

.order-image{
    text-align: center;
    img{
        margin-bottom: 30px;
    }
    .inputs{
        display: flex;
        flex-direction: column;
        align-items: center;
        input{
            margin-bottom: 20px;
        }
    }
}

.request-item{
    display: flex;
    input{
        &:first-child{
            flex-basis: 70%;
        }
        &:last-child{
            text-align: center;
            flex-basis: 28%;
            margin-left: 2%;
        }
    }
}