// 1.2. Button Styles
.btn{
    font-size: 12px;
    border-radius: 0;
    text-transform: uppercase;
    padding: 10px 20px;
    border: none;
    border-radius: 2px;
    font-family: $primary-font;
    font-weight: 500;
    font-size: 20px;
}

.btn-primary{
    background: $primary-color;
    color: $white;
    position: relative;
    z-index: 1;
    font-size: 14px;
    &:active{
        background: $primary-color !important;
    }
    &:hover{
        background: $primary-color;
    }
}

.btn-sm{
    padding: 15px 30px;
}

.btn-dark{
    background-color: #313131;
}